/* ==========================================================================
   Reset CSS -    
   Based on modern-normalize v2.0
   ========================================================================== */

/* ===== DOCUMENT ===== */
/*    */

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  /*   */
  scroll-behavior: smooth;

  /*     */
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  text-size-adjust: 100%;

  /*    */
  -moz-tab-size: 4;
  tab-size: 4;

  /*    */
  font-size: 16px;
}

/* ===== BODY ===== */
/*    */

body {
  margin: 0;
  padding: 0;
  font-family:
    system-ui,
    -apple-system,
    "Segoe UI",
    Roboto,
    sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  color: inherit;
  background-color: transparent;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* ===== HEADINGS ===== */
/*    */

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}

/* ===== PARAGRAPHS & LISTS ===== */
/*     */

p {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

/* ===== LINKS ===== */
/*   */

a {
  color: inherit;
  text-decoration: none;
  background-color: transparent;
}

a:hover {
  text-decoration: none;
}

/* ===== MEDIA ===== */
/*    */

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
  height: auto;
}

img {
  border-style: none;
}

/* ===== FORMS ===== */
/*    */

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  padding: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: inherit;
  color: inherit;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
  appearance: button;
  background-color: transparent;
  background-image: none;
  border: none;
  cursor: pointer;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

legend {
  padding: 0;
}

textarea {
  overflow: auto;
  resize: vertical;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

/* ===== TABLES ===== */
/*   */

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

/* ===== INTERACTIVE ===== */
/*   */

details {
  display: block;
}

summary {
  display: list-item;
}

/* ===== MISC ===== */
/*   */

/* Hidden attribute -   !important  */
html [hidden] {
  display: none;
}

abbr[title] {
  text-decoration: underline dotted;
  border-bottom: none;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp,
pre {
  font-family: ui-monospace, monospace;
  font-size: 1em;
}

pre {
  overflow: auto;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* ===== FOCUS STYLES ===== */
/*   */

:focus {
  outline: 0;
}

:focus-visible {
  outline: 2px solid currentColor;
  outline-offset: 2px;
}

/* ===== SELECTION ===== */
/*    */

::selection {
  background-color: #b3d4fc;
  color: #000;
  text-shadow: none;
}

/* ===== PRINT ===== */
/*   */

@media print {
  /* Print styles -   !important  */
  html *,
  html *::before,
  html *::after {
    background: transparent;
    color: #000;
    box-shadow: none;
    text-shadow: none;
  }

  a,
  a:visited {
    text-decoration: underline;
  }

  abbr[title]::after {
    content: " (" attr(title) ")";
  }

  html pre {
    white-space: pre-wrap;
  }

  pre,
  blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }

  thead {
    display: table-header-group;
  }

  tr,
  img {
    page-break-inside: avoid;
  }

  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }

  h2,
  h3 {
    page-break-after: avoid;
  }
}
