/**
 * Tarot Specific Styles
 *    
 */

/*    */
.card-deck {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  gap: var(--spacing-2);
  margin: var(--spacing-6) 0;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

/*   */
.tarot-card {
  width: 80px;
  height: 120px;
  border-radius: var(--radius-lg);
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  transform-style: preserve-3d;
  background: var(--color-white);
  box-shadow: var(--shadow-md);
  display: flex;
  align-items: center;
  justify-content: center;
}

.tarot-card:hover:not(.selected) {
  transform: translateY(-5px);
  box-shadow: var(--shadow-lg);
}

.tarot-card.card-back {
  background: linear-gradient(45deg, #667eea 0%, #764ba2 100%);
  color: var(--color-white);
  font-size: 2rem;
}

.tarot-card.selected {
  cursor: default;
}

.tarot-card.flipping {
  animation: cardFlip 0.6s ease;
}

@keyframes cardFlip {
  0% {
    transform: rotateY(0);
  }
  50% {
    transform: rotateY(90deg);
  }
  100% {
    transform: rotateY(0);
  }
}

/*   */
.card-content {
  text-align: center;
  padding: var(--spacing-2);
}

.card-emoji {
  font-size: 2rem;
  margin-bottom: var(--spacing-1);
}

.card-name {
  font-size: 0.75rem;
  font-weight: var(--font-weight-medium);
  color: var(--color-gray-800);
}

.card-content.reversed {
  transform: rotate(180deg);
}

.reversed-indicator {
  font-size: 0.625rem;
  color: var(--color-red-600);
  margin-top: var(--spacing-1);
}

/*    */
.selected-cards {
  margin: var(--spacing-6) 0;
  text-align: center;
}

.selected-card-list {
  display: flex;
  gap: var(--spacing-2);
  justify-content: center;
  flex-wrap: wrap;
  margin-top: var(--spacing-3);
}

.mini-card {
  background: var(--color-white);
  border: 1px solid var(--color-gray-300);
  border-radius: var(--radius-md);
  padding: var(--spacing-2);
  display: flex;
  align-items: center;
  gap: var(--spacing-1);
  font-size: 0.875rem;
}

.mini-card.reversed {
  border-color: var(--color-red-300);
  background: var(--color-red-50);
}

.mini-emoji {
  font-size: 1.25rem;
}

.mini-reversed {
  color: var(--color-red-600);
  font-weight: var(--font-weight-medium);
}

/*   */
.spread-layout {
  display: grid;
  gap: var(--spacing-4);
  margin: var(--spacing-6) 0;
}

/*    */
.spread-layout.- {
  grid-template-columns: 1fr;
  max-width: 300px;
  margin-left: auto;
  margin-right: auto;
}

/*    */
.spread-layout.- {
  grid-template-columns: repeat(3, 1fr);
}

/*    */
.spread-layout.- {
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, auto);
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.spread-layout.- .reading-card:nth-child(1) {
  grid-column: 2;
  grid-row: 2;
}

.spread-layout.- .reading-card:nth-child(2) {
  grid-column: 2;
  grid-row: 2;
  margin-top: 20px;
  margin-left: 20px;
}

.spread-layout.- .reading-card:nth-child(3) {
  grid-column: 2;
  grid-row: 3;
}

.spread-layout.- .reading-card:nth-child(4) {
  grid-column: 1;
  grid-row: 2;
}

.spread-layout.- .reading-card:nth-child(5) {
  grid-column: 2;
  grid-row: 1;
}

.spread-layout.- .reading-card:nth-child(6) {
  grid-column: 3;
  grid-row: 2;
}

.spread-layout.- .reading-card:nth-child(7) {
  grid-column: 4;
  grid-row: 4;
}

.spread-layout.- .reading-card:nth-child(8) {
  grid-column: 4;
  grid-row: 3;
}

.spread-layout.- .reading-card:nth-child(9) {
  grid-column: 4;
  grid-row: 2;
}

.spread-layout.- .reading-card:nth-child(10) {
  grid-column: 4;
  grid-row: 1;
}

/*   */
.reading-card {
  background: var(--color-gray-50);
  border-radius: var(--radius-lg);
  padding: var(--spacing-4);
  text-align: center;
}

.card-position {
  font-size: 0.875rem;
  color: var(--color-gray-600);
  margin-bottom: var(--spacing-2);
  font-weight: var(--font-weight-medium);
}

.card-visual {
  background: var(--color-white);
  border-radius: var(--radius-md);
  padding: var(--spacing-3);
  margin-bottom: var(--spacing-3);
  box-shadow: var(--shadow-sm);
}

.card-visual.reversed {
  background: var(--color-red-50);
  border: 1px solid var(--color-red-200);
}

.reversed-badge {
  display: inline-block;
  background: var(--color-red-600);
  color: var(--color-white);
  font-size: 0.75rem;
  padding: 2px 8px;
  border-radius: var(--radius-sm);
  margin-top: var(--spacing-2);
}

.card-interpretation {
  font-size: 0.875rem;
  line-height: 1.6;
  color: var(--color-gray-700);
}

/*   */
.tarot-animation {
  text-align: center;
  padding: var(--spacing-6);
}

.tarot-animation h3 {
  font-size: 1.5rem;
  margin-bottom: var(--spacing-2);
}

.tarot-animation p {
  color: var(--color-gray-600);
  margin-bottom: var(--spacing-4);
}

/* AI  */
.ai-badge {
  display: inline-block;
  background: var(--color-purple-100);
  color: var(--color-purple-800);
  padding: var(--spacing-2) var(--spacing-3);
  border-radius: var(--radius-md);
  font-size: 0.875rem;
  font-weight: var(--font-weight-medium);
  margin: var(--spacing-4) 0;
}

/*   */
.spread-description {
  background: var(--color-gray-50);
  border-radius: var(--radius-md);
  padding: var(--spacing-4);
  margin-top: var(--spacing-3);
}

.spread-description ul {
  list-style: none;
  padding: 0;
  margin: var(--spacing-2) 0 0 0;
}

.spread-description li {
  padding: var(--spacing-1) 0;
  color: var(--color-gray-700);
  font-size: 0.875rem;
}

/*   */
@media (max-width: 768px) {
  .card-deck {
    grid-template-columns: repeat(auto-fit, minmax(60px, 1fr));
  }

  .tarot-card {
    width: 60px;
    height: 90px;
  }

  .card-emoji {
    font-size: 1.5rem;
  }

  .card-name {
    font-size: 0.625rem;
  }

  .spread-layout.- {
    grid-template-columns: 1fr;
  }

  .spread-layout.- {
    grid-template-columns: repeat(2, 1fr);
  }

  /*    -   !important  */
  .spread-layout.- .reading-card {
    grid-column: auto;
    grid-row: auto;
    margin: 0;
  }
}
