/* ==========================================================================
   Typography -   
     
   ========================================================================== */

/* ===== BASE TYPOGRAPHY ===== */
/*    */

html {
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-feature-settings: "kern" 1;
  font-kerning: normal;
}

body {
  font-family: var(--font-family-korean);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-regular);
  line-height: var(--line-height-korean);
  letter-spacing: var(--letter-spacing-korean);
  color: var(--text-primary);

  /*   */
  word-break: keep-all;
  word-wrap: break-word;
  overflow-wrap: break-word;
}

/* ===== HEADINGS ===== */
/*   */

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: var(--space-4);
  font-weight: var(--font-weight-semibold);
  line-height: var(--line-height-tight);
  letter-spacing: var(--letter-spacing-tight);
  color: var(--text-primary);
}

h1 {
  font-size: var(--font-size-4xl);
  font-weight: var(--font-weight-bold);
}

h2 {
  font-size: var(--font-size-3xl);
}

h3 {
  font-size: var(--font-size-2xl);
}

h4 {
  font-size: var(--font-size-xl);
}

h5 {
  font-size: var(--font-size-lg);
}

h6 {
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-medium);
}

/* ===== PARAGRAPHS ===== */
/*   */

p {
  margin-top: 0;
  margin-bottom: var(--space-4);
  line-height: var(--line-height-korean);
}

/*    */
p + p {
  margin-top: var(--space-4);
}

/* ===== LINKS ===== */
/*   */

a {
  color: var(--text-link);
  text-decoration: none;
  transition: var(--transition-colors);
}

a:hover {
  color: var(--text-link-hover);
  text-decoration: underline;
}

a:focus-visible {
  outline: var(--focus-ring-width) solid var(--focus-ring-color);
  outline-offset: var(--focus-ring-offset);
  border-radius: var(--radius-sm);
}

/* ===== LISTS ===== */
/*   */

ul,
ol {
  margin-top: 0;
  margin-bottom: var(--space-4);
  padding-left: var(--space-6);
}

ul {
  list-style-type: disc;
}

ol {
  list-style-type: decimal;
}

li {
  margin-bottom: var(--space-2);
  line-height: var(--line-height-korean);
}

/*   */
ul ul,
ol ol,
ul ol,
ol ul {
  margin-top: var(--space-2);
  margin-bottom: var(--space-2);
}

/* ===== EMPHASIS ===== */
/*   */

strong,
b {
  font-weight: var(--font-weight-semibold);
}

em,
i {
  font-style: italic;
}

mark {
  background-color: var(--color-warning-light);
  color: var(--text-primary);
  padding: 0.125em 0.25em;
  border-radius: var(--radius-sm);
}

/* ===== QUOTES ===== */
/*   */

blockquote {
  margin: var(--space-6) 0;
  padding: var(--space-4) var(--space-6);
  border-left: var(--border-4) solid var(--border-primary);
  background-color: var(--bg-secondary);
  font-style: italic;
}

blockquote p:last-child {
  margin-bottom: 0;
}

q {
  quotes: "" " " "" "'" "'";
}

/* ===== CODE ===== */
/*   */

code,
kbd,
samp,
pre {
  font-family: var(--font-mono);
  font-size: 0.875em;
}

code {
  background-color: var(--bg-tertiary);
  color: var(--text-primary);
  padding: 0.125em 0.375em;
  border-radius: var(--radius-sm);
}

pre {
  display: block;
  margin: var(--space-4) 0;
  padding: var(--space-4);
  background-color: var(--bg-tertiary);
  border-radius: var(--radius-md);
  overflow-x: auto;
  line-height: var(--line-height-normal);
}

pre code {
  background-color: transparent;
  padding: 0;
  font-size: inherit;
}

/* ===== SMALL TEXT ===== */
/*   */

small {
  font-size: var(--font-size-sm);
  line-height: var(--line-height-normal);
}

/* ===== ABBREVIATIONS ===== */
/*   */

abbr[title] {
  text-decoration: underline dotted;
  cursor: help;
}

/* ===== TIME ===== */
/*   */

time {
  color: var(--text-secondary);
}

/* ===== HORIZONTAL RULE ===== */
/*  */

hr {
  margin: var(--space-8) 0;
  border: 0;
  border-top: var(--border-1) solid var(--border-primary);
}

/* ===== RESPONSIVE TYPOGRAPHY ===== */
/*   */

@media (max-width: 768px) {
  /*     */
  h1 {
    font-size: var(--font-size-3xl);
  }

  h2 {
    font-size: var(--font-size-2xl);
  }

  h3 {
    font-size: var(--font-size-xl);
  }

  h4 {
    font-size: var(--font-size-lg);
  }

  h5 {
    font-size: var(--font-size-base);
  }

  h6 {
    font-size: var(--font-size-sm);
  }

  /*    */
  p {
    margin-bottom: var(--space-3);
  }
}

/* ===== PRINT TYPOGRAPHY ===== */
/*   */

@media print {
  body {
    font-size: 12pt;
    line-height: 1.5;
    color: #000;
  }

  h1 {
    font-size: 24pt;
  }
  h2 {
    font-size: 20pt;
  }
  h3 {
    font-size: 16pt;
  }
  h4 {
    font-size: 14pt;
  }
  h5,
  h6 {
    font-size: 12pt;
  }

  a {
    color: #000;
    text-decoration: underline;
  }

  a[href^="http"]:after {
    content: " (" attr(href) ")";
    font-size: 10pt;
  }
}
